* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Tahoma, sans-serif;
}

body {
    background: linear-gradient(to right, #ff9068, #fd746c);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.app {
    margin: auto;
}

.app h1 {
    text-align: center;
    color: #fafafa;
    margin: 35px 10px;
    font-size: 36px;
}

.todo-form {
    justify-content: center;
    display: flex;
    align-items: center;
    gap: 30px;
}

.app .todo-input {
    width: clamp(150px, 40vw, 300px);
    padding: 0 10px;
    text-transform: capitalize;
}

.app .todo-input:focus {
    background-color: #ebebeb;
}

.app :is(.addTodo-button, .todo-input, .todo-select) {
    height: 43px;
    border: 0;
    background-color: #fafafa;
    font-size: 14px;
    outline: 0;
}

.app .addTodo-button {
    padding: 3px 15px;
    color: #fafafa;
    cursor: pointer;
    background-color: #f50166;
}

.app .addTodo-button:hover {
    background-color: #525dfdfb;
}

.app .todo-select {
    padding: 0 16px;
    cursor: pointer;
}

.todo-container {
    display: flex;
    justify-content: center;
    margin-top: 26px;
}
.todo-list {
    list-style-type: none;
}
.todo-list .todo {
    display: flex;
    width: clamp(180px, 55vw, 300px);
}
.todo-list .todo + .todo {
    margin-top: 8px;
}
.todo-list .todo.completed  {
    text-decoration: line-through;
    opacity: .7;
}
.todo-list .todo p {
    background-color: #fafafa;
    padding: 10px;
    flex-grow: 1;
}
.todo-list .todo button {
    padding: 0 15px;
    border: 0;
    outline: 0;
    font-size: 16px;
    cursor: pointer;
}
.todo-list .todo button.complete {
    background-color: #0bd4a2;
    color: #fff;
}
.todo-list .todo button.complete:hover {
    background-color: #10be93;
}
.todo-list .todo button.remove {
    background-color: #ff004c;
    color: #fff;
}
.todo-list .todo button.remove:hover {
    background-color: #eb054a;
}